#paper-layout {
    background-color: white;
    outline-style: solid;
    outline-color: black;
    outline-width: 3px;
    max-width: 100%; 
    max-height: 100%;
    width: auto;
    height: auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); 
    position: relative;
    object-fit: contain; 
}

#paper-layout-container {
    margin-inline: 1%;
    height: 30vw;
    width: auto;
}